import React from "react";
import Navbare from "../navbar";
import "../etabs.css";
import LogoStudio from "../img/androidStudiologo.png";
import LogoFirebase from "../img/Firebaselogo.png";
import LogoK from "../img/Kotlinlogo.png";
import Tab1 from "../img/tab1.png";
import Tab2 from "../img/tab2.png";
import Etabs from "../img/etab.mp4";
import Tab4 from "../img/tab4.png";
import Tab5 from "../img/tab4.png";
import AOS from "aos";

// You can also use <link> for styles
// ..
AOS.init();

export default function etabs() {
  return (
    <div>
      <div className=" lg:-mt-36">
        <Navbare />
      </div>
      <div className="title">Etabs</div>
      <div className="subtitle"> lapplication pour tout musicien</div>
      <div className="circle-container">
        <div className="circle">
          <img src={LogoStudio} alt="Android Studio Logo" />
        </div>
        <div className="circle">
          <img src={LogoFirebase} alt="Firebase Logo" />
        </div>
        <div className="circle">
          <img src={LogoK} alt="Kotlin Logo" />
        </div>
      </div>
      <div className=" lg:mt-52 font-openSans text justify-center lg:ml-96  sm:ml-12 lg:mr-96  sm:mr-12 mt-28 ">
        <div className=" p-5 backdrop-blur-sm bg-white/10  rounded rounded-xl">
          En tant que musicien expriment, j'ai longtemps cherch l'application
          idale pour lire mes tablatures et partitions, avec des
          fonctionnalits telles que l'enregistrement, le tri par difficult ou
          instrument, ainsi que la possibilit de sauvegarder mes favoris.
          Malheureusement, aucune des applications existantes ne rpondait  mes
          besoins spcifiques.
        </div>
      </div>
      <div className="  lg:mb-96 sm:mb-0 font-openSans text justify-center lg:ml-96  sm:ml-12 lg:mr-96  sm:mr-12 mt-16 ">
        <div className="p-5 backdrop-blur-sm bg-white/10 rounded rounded-xl">
          C'est pourquoi j'ai dcid de crer ma propre application, en
          collaboration avec un ami pour le design, afin de grer mes tablatures
          de manire efficace. Nous avons accord une attention particulire 
          l'ergonomie et  l'utilisabilit de l'application, qui a t
          dveloppe en utilisant Kotlin sur Android Studio. Pour stocker les
          tablatures au format JPG, nous avons opt pour Firebase.
        </div>
      </div>
      <div className="grid grid-cols-1 sm:grid-cols-1 sm:align-middle lg:grid-cols-5 gap-6 justify-center items-center mt-16 lg:-mt-28">
        <img
          data-aos="fade-up"
          src={Tab1}
          alt="image 1"
          className="h-500 w-auto lg:w-11/12 image-hover"
        />
        <img
          data-aos="fade-up"
          src={Tab2}
          alt="image 2"
          className="h-500 w-auto lg:w-11/12 image-hover"
        />
        <video
          data-aos="fade-up"
          src={Etabs}
          alt="video"
          className="h-500 w-auto lg:w-auto video-container"
          height={500}
          width={300}
          autoPlay
          loop
          controls={false}
        />
        <img
          data-aos="fade-up"
          src={Tab4}
          alt="image 4"
          className="h-500 w-auto lg:w-11/12 image-hover"
        />
        <img
          data-aos="fade-up"
          src={Tab5}
          alt="image 5"
          className="h-500 w-auto lg:w-11/12 image-hover"
        />
      </div>
    </div>
  );
}
